import { cn } from "@/lib/utils";

type StatusVariant = "ok" | "alerta" | "critico";

interface StatusPillProps {
  status: StatusVariant;
  children?: React.ReactNode;
}

const LABELS: Record<StatusVariant, string> = {
  ok: "Saudável",
  alerta: "Atenção",
  critico: "Crítico",
};

export function StatusPill({ status, children }: StatusPillProps) {
  const label = children ?? LABELS[status];

  return (
    <span
      className={cn(
        "inline-flex items-center rounded-full px-2 py-0.5 text-xs font-medium",
        status === "ok" &&
          "bg-[oklch(0.96_0.05_170)] text-[oklch(0.33_0.07_170)]",
        status === "alerta" &&
          "bg-[oklch(0.98_0.07_95)] text-[oklch(0.42_0.12_95)]",
        status === "critico" &&
          "bg-[oklch(0.95_0.16_25)] text-[oklch(0.45_0.22_25)]"
      )}
    >
      {label}
    </span>
  );
}


